import './Users.css'

import DefaultAvatar from '../../assets/images/DefaultAvatar.png'
import React from 'react'
import { NavLink } from 'react-router-dom'




const Users = (props) => {

	let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize)
	let pages = []

	for (let i = 1; i <= pagesCount; i++) {
		pages.push(i)
	}

	let pagination = pages.map(page => {
		return <span onClick={() => { props.onPageChenged(page) }} className={props.currentPage === page ? 'selected-page' : ''}>{page}</span>
	})

	const users = props.users.map(user =>

		<div key={user.id} className="user">
			<div className="user__logo">

				<NavLink to={'/Profile/' + user.id}>
					<img src={user.photos.small != null ? user.photos.small : DefaultAvatar} alt="User Avatar" className="user__avatar" />
				</NavLink>

				{

					// true 
					user.followed
						? <button disabled={props.followingInProgress.some(id => id === user.id)} onClick={() => { props.unFollow(user.id) }}
							className='btn unfollow'>  Unfollow</button>


						: <button disabled={props.followingInProgress.some(id => id === user.id)} onClick={() => { props.follow(user.id) }}
							className='btn follow'>Follow</button>

				}
			</div>
			<div className="user__content">
				<div className="user__top">
					<div className="user__name">{user.name}</div>
				</div>
				<div className="user__label">{user.status}</div>
			</div>
		</div>

	)

	return <div className="users">
		<h2 className="title">Users</h2>
		<div className="pagination-container">
			{pagination}

		</div>
		<div className="users__row">
			{users}
		</div>
		<div className="btn">SHOW MORE</div>
	</div>
}

export default Users