import React from 'react'
import '../../Profile/Profile.css'
import { Field, reduxForm } from 'redux-form'
import { maxLength, required } from '../../../utilites/validator'
import { Element } from '../../common/FormControls'


const AddPost = (props) => {
	const onSubmit = (value) => {
		props.onAddPost(value.newPost)
	}

	return (
		<AddPostFormRedux onSubmit={onSubmit} />
	)
}

const AddPostForm = (props) => {
	let maxLength25 = maxLength(30)
	const Textarea = Element('textarea')
	return (
		<form onSubmit={props.handleSubmit} className="add-post">
			<Field validate={[required, maxLength25]} component={Textarea} name='newPost' placeholder='Enter new post' className='input' />
			<button className='btn'>Add</button>
		</form>
	)
}

const AddPostFormRedux = reduxForm({ form: 'newPost' })(AddPostForm)



export default AddPost
