import './MyProfile.css'
import Post from './Post/Post'
// import ProfileStatus from './ProfileStatus'

import AddPostContainer from './AddPost/AddPostContainer'
import ProfileStatusWithHooks from './ProfileStatusWithHooks'


const MyProfile = ({ posts, myProfile, updateStatus, status }) => {
	const postsAll = posts.map(post => {
		return <Post img={post.img} text={post.text} key={post.id} />
	})
	return (
		<div className="my-profile">
			<div className="my-profile__content">
				<img src={myProfile.img} alt="Avatar" className='my-profile__img' />
				<div className="my-profile__body">
					<div className="my-profile__name">{myProfile.name}</div>
					<div className="my-profile__age">Age: {myProfile.age}</div>
					<div className="my-profile__country">Country: {myProfile.country}</div>
					<ProfileStatusWithHooks updateStatus={updateStatus} status={status} />
				</div>
			</div>
			<h2 className="title">Your Posts</h2>
			<div className="profile__posts">
				{postsAll}
			</div>
			<AddPostContainer />
		</div>
	)
}

export default MyProfile