const ADD_MESSAGE = 'ADD_MESSAGE';
const UPDATE_NEW_MESSAGE = 'UPDATE_NEW_MESSAGE';

let initialState = {
	friends: [
		{ id: 1, name: 'Sasha', path: '1' },
		{ id: 2, name: 'Maxim', path: '2' },
		{ id: 3, name: 'Kiril', path: '3' },
		{ id: 4, name: 'Max', path: '4' }
	],
	messages: [
		{ id: 1, img: 'https://cdn-icons-png.flaticon.com/512/149/149071.png', message: 'some message' },
		{ id: 2, img: 'https://cdn-icons-png.flaticon.com/512/149/149071.png', message: 'Hello. keep go to your dreem!!!' },
	],
}

const dialogsReduser = (state = initialState, action) => {


	if (action.type === ADD_MESSAGE) {

		let num = state.messages[state.messages.length - 1].id;

		let newMessage = {
			id: ++num,
			img: 'https://cdn-icons-png.flaticon.com/512/149/149071.png',
			message: action.newMessage
		}

		return {
			...state,
			messages: [...state.messages, newMessage]
		}
	}

	return state
}


export default dialogsReduser