import './MyProfile.css'
import React, { useEffect, useState } from 'react'

const ProfileStatusWithHooks = (props) => {

	let [editMode, setEditMode] = useState(false)
	let [status, seStatus] = useState(props.status)

	useEffect(() => {
		seStatus(props.status)
	}, [props.status])

	const activeMode = () => {
		setEditMode(true)
	}

	const deActivateEditMode = () => {
		setEditMode(false)
		props.updateStatus(status)
	}

	const onChangeStatus = (e) => {
		seStatus(e.target.value)
	}
	return (
		<div className="profile-status">
			{editMode
				? <input onBlur={deActivateEditMode} onChange={onChangeStatus}
					autoFocus={true} type="text" className="profile-status__inp" value={status} />
				: <label onDoubleClick={activeMode} className="profile-status__label"> About Me: {props.status}</label>
			}


		</div>
	)

}


export default ProfileStatusWithHooks