import './Header.css'
import { NavLink } from 'react-router-dom'

const Header = ({ auth, login, logout }) => {


	return (
		<header className="header">
			<div className="header__container">
				<img src="https://upload.wikimedia.org/wikipedia/commons/a/a0/UTDI-logo.png" alt="Header logo" className="header__logo" />
				<ul className='header__list'>
					<li> <a href="#">Text</a></li>
					<li> <a href="#">Text</a></li>
					<li> <a href="#">Text</a></li>
					<li> <a href="#">Text</a></li>
				</ul>
				{auth
					? <div>{login}<button onClick={logout} className='btn'> Log out</button></div>
					: <NavLink to={'/login'}>Login</NavLink>

				}

			</div>
		</header>
	)
}

export default Header