import './Aside.css'
import Friend from './Friend/Friend'
import { NavLink } from 'react-router-dom'

const Aside = (props) => {
	const friends = props.friends.map(friend => {
		return <Friend img={friend.img} key={friend.id} name={friend.name} />
	})
	return (
		<div className="aside">

			<nav className="aside__nav">
				<NavLink to='/Profile' className="aside__link">Profile</NavLink>
				<NavLink to='/Dialogs' className="aside__link">Messeges</NavLink>
				<NavLink to='/Users' className="aside__link">Users</NavLink>
				<NavLink className="aside__link">News</NavLink>
				<NavLink className="aside__link">Music</NavLink>
				<NavLink className="aside__link">Settings</NavLink>

			</nav>
			<h2 className="title">Friends</h2>
			<div className="aside__friends">
				{friends}
			</div>
		</div>
	)
}

export default Aside