import { profileAPI } from "../api/api";
const ADD_POST = 'ADD_POST';
const UPDATE_NEW_POST = 'UPDATE_NEW_POST'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'

let initialState = {
	myProfile: {
		id: 28233,
		img: 'https://www.pngarts.com/files/11/Avatar-PNG-Free-Download.png',
		name: 'Vitaliy',
		age: 18,
		country: 'Ukraine',
		status: 'Frontend Developer'
	},
	posts: [
		{ id: 1, img: 'https://www.pngarts.com/files/11/Avatar-PNG-Free-Download.png', text: 'some text' },
		{ id: 2, img: 'https://cdn-icons-png.flaticon.com/512/5968/5968292.png', text: 'i learn js' },
	],

	setUserProfile: null,
	status: null
}

const profileReduser = (state = initialState, action) => {
	switch (action.type) {
		case ADD_POST:
			let num = state.posts[state.posts.length - 1].id
			let newPost = {
				id: ++num,
				img: 'https://cdn-icons-png.flaticon.com/512/5968/5968292.png',
				text: action.newPost
			}

			return {
				...state,
				posts: [...state.posts, newPost]
			}

		case SET_USER_PROFILE:
			return { ...state, setUserProfile: action.userData }

		case SET_STATUS:
			return { ...state, status: action.status }
		default:
			return state
	}

}
const setUser = (userData) => { return { type: SET_USER_PROFILE, userData } }
const setStatus = (status) => { return { type: SET_STATUS, status } }


export const getStatus = (userId) => async (dispatch) => {
	let response = await profileAPI.getStatus(userId)
	dispatch(setStatus(response.data));
}




export const updateStatus = (status) => async (dispatch) => {
	let response = await profileAPI.updateStatus(status)
	if (response.data.resultCode === 0) {
		dispatch(setStatus(status));
	}

}

export const getProfile = (userId) => async (dispatch) => {
	let data = await profileAPI.getProfile(userId)
	dispatch(setUser(data));

}

export default profileReduser