import React from 'react'
import '../Dialogs.css'
import { Field, reduxForm } from 'redux-form'
import { Element } from '../../common/FormControls'
import { maxLength, required } from '../../../utilites/validator'



const AddMessage = ({ onAddMessage }) => {
	const onSubmit = (value) => {
		onAddMessage(value.newMessage)
	}

	return (
		<AddMessageFormRedux onSubmit={onSubmit} />
	)
}

const AddMessageForm = ({ handleSubmit }) => {
	let maxLength10 = maxLength(10)
	const Textarea = Element('textarea')

	return (
		<form onSubmit={handleSubmit} className="add-message">
			<Field component={Textarea} validate={[required, maxLength10]} name='newMessage' placeholder='Enter message' className='input' />
			<button className='btn'>Submit</button>
		</form>
	)
}
const AddMessageFormRedux = reduxForm({ form: 'dialogAddMessage', })(AddMessageForm)

export default AddMessage