import { usersAPI } from "../api/api"
const FOLLOW = 'FOLLOW'
const UN_FOLLOW = 'UN_FOLLOW'
const SET_USERS = 'SET_USERS'
const SET_USERS_COUNT = 'SET_USERS_COUNT'
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE'
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING'
const TOGGLE_IS_FOLLOWING_PROG = 'TOGGLE_IS_FOLLOWING_PROG'

let initialState = {
	users: [],
	pageSize: 5,
	totalUsersCount: 10,
	currentPage: 2,
	isFetching: false,
	followingInProgress: []
}

const usersReduser = (state = initialState, action) => {

	switch (action.type) {
		case FOLLOW:
			return {
				...state,
				users: state.users.map(u => {
					if (u.id === action.id) {
						return { ...u, followed: true }

					}
					return u
				})
			}

		case UN_FOLLOW:

			return {
				...state,
				users: state.users.map(u => {
					if (u.id === action.id) {
						return { ...u, followed: false }
					}
					return u

				})
			}
		case SET_USERS:
			return { ...state, users: action.users }


		case SET_CURRENT_PAGE:
			return { ...state, currentPage: action.currentPage }

		case SET_USERS_COUNT:
			return { ...state, totalUsersCount: action.totalCount }


		case TOGGLE_IS_FETCHING:
			return { ...state, isFetching: action.isFetching }


		case TOGGLE_IS_FOLLOWING_PROG:
			return {
				...state,
				followingInProgress: action.isFetching
					? [...state.followingInProgress, action.userId]
					: state.followingInProgress.filter(id => id != action.userId)
			}


		default:
			return state
	}

}

export const followSuccuss = (id) => { return { type: FOLLOW, id } }
export const unFollowSuccuss = (id) => { return { type: UN_FOLLOW, id } }
export const setUsers = (users) => { return { type: SET_USERS, users } }
export const setCurrentPage = (currentPage) => { return { type: SET_CURRENT_PAGE, currentPage } }
export const setTotalUsersCount = (totalCount) => { return { type: SET_USERS_COUNT, totalCount } }
export const toggleIsFetching = (isFetching) => { return { type: TOGGLE_IS_FETCHING, isFetching } }
export const toggleFollowingProgress = (isFetching, userId) => { return { type: TOGGLE_IS_FOLLOWING_PROG, isFetching, userId } }

export const getUsers = (totalUsersCount, pageSize) => async (dispatch) => {
	dispatch(toggleIsFetching(true))
	dispatch(setCurrentPage(pageSize))
	let data = await usersAPI.getUsers(totalUsersCount, pageSize)
	dispatch(toggleIsFetching(false))
	dispatch(setUsers(data.items))


}
export const unFollow = (userId) => async (dispatch) => {
	dispatch(toggleFollowingProgress(true, userId))
	let response = await usersAPI.unFollow(userId)
	if (response.data.resultCode == 0) {
		dispatch(unFollowSuccuss(userId))
	}
	dispatch(toggleFollowingProgress(false, userId))
}
export const follow = (userId) => async (dispatch) => {

	dispatch(toggleFollowingProgress(true, userId))
	let data = await usersAPI.follow(userId)
	if (data.resultCode == 0) {
		dispatch(followSuccuss(userId))
	}

	dispatch(toggleFollowingProgress(false, userId))
}

export default usersReduser