import { stopSubmit } from "redux-form"
import { authAPI } from "../api/api"

const SET_USER_DATA = 'SET_USER_DATA'

let initialState = {
	userId: null,
	login: null,
	email: null,
	isAuth: false

}


const authReduser = (state = initialState, action) => {
	switch (action.type) {
		case SET_USER_DATA:

			return { ...state, ...action.data }

		default:
			return state;
	}
}

const setUserData = (userId, login, email, isAuth) => { return { type: 'SET_USER_DATA', data: { userId, login, email, isAuth } } }

export const getAuthData = () => async (dispatch) => {

	let response = await authAPI.me()
	if (response.data.resultCode === 0) {
		let { id, login, email } = response.data.data
		dispatch(setUserData(id, login, email, true));
	}

}


export const login = (email, password, rememberMe) => async (dispatch) => {
	let response = await authAPI.login(email, password, rememberMe);

	if (response.data.resultCode === 0) {
		dispatch(getAuthData());
	} else {
		let messsage = response.data.messages.length > 0 ? response.data.messages[0] : 'Some Error'
		dispatch(stopSubmit('login', { _error: messsage }))
	}


}
export const logout = () => async (dispatch) => {
	let response = await authAPI.logout()
	if (response.data.resultCode === 0) {
		dispatch(setUserData(null, null, null, false));
	}

}
export default authReduser