import AddMessageContainer from './AddMessage/AddMessageContainer'
import './Dialogs.css'
import Friend from './Friend/Friend'
import Message from './Message/Message'



const Dialogs = ({ friends, messages }) => {
	let friendsAll = friends.map(friend => {
		return <Friend key={friend.id} name={friend.name} path={friend.path} />
	})

	let messagesAll = messages.map(message => {
		return <Message img={message.img} key={message.id} message={message.message} />
	})






	return (
		<div className="dialogs">
			<h2 className="title">Dialogs</h2>
			<div className="dialogs__row">
				<div className="dialogs__friends">
					{friendsAll}
				</div>
				<div className="dialogs__messages">
					{messagesAll}
					<AddMessageContainer />
				</div>
			</div>
		</div>
	)
}



export default Dialogs
