import React from 'react';
import { connect } from 'react-redux';
import Users from './Users';
import Preloader from '../common/Preloader';
import { withAuthRedirect } from '../../hoc/WithAuthRedirect';
import { compose } from 'redux';
import {
	// followSuccuss, unFollowSuccuss,
	setCurrentPage,
	toggleFollowingProgress,
	getUsers,
	follow, unFollow
} from '../../redux/users-reduser';
import { getCurrentPage, getCurrentUsers, getFollowingInProgress, getIsFetching, getPageSize, getTotalUsersCount } from '../../redux/users-selectors';



const mapStateToProps = (state) => {
	return {
		users: getCurrentUsers(state),
		pageSize: getPageSize(state),
		totalUsersCount: getTotalUsersCount(state),
		currentPage: getCurrentPage(state),
		isFetching: getIsFetching(state),
		followingInProgress: getFollowingInProgress(state),
	}
}

class UsersConatiner extends React.Component {
	componentDidMount() {
		this.props.getUsers(this.props.totalUsersCount, this.props.pageSize)

	}
	onPageChenged = (pageNumber) => {
		this.props.getUsers(this.props.totalUsersCount, pageNumber)
	}

	render() {

		return (<>

			{this.props.isFetching ? <Preloader /> : null}

			<Users
				isAuth={this.props.isAuth}
				users={this.props.users} pageSize={this.props.pageSize} totalUsersCount={this.props.totalUsersCount} currentPage={this.props.currentPage}
				follow={this.props.follow} unFollow={this.props.unFollow} setUsers={this.props.setUsers}
				setCurrentPage={this.props.setCurrentPage} setTotalUsersCount={this.props.setTotalUsersCount}
				onPageChenged={this.onPageChenged} followingInProgress={this.props.followingInProgress}
			// {...this.props}
			/>
		</>
		)
	}
}



export default compose(
	connect(mapStateToProps, {
		setCurrentPage,
		toggleFollowingProgress,
		getUsers, follow, unFollow
	}),
	withAuthRedirect
)(UsersConatiner)