import './Login.css'
import { Field, reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { login } from '../../redux/auth-reduser'
import { Element } from '../common/FormControls'
import { required } from '../../utilites/validator'
import { Navigate } from 'react-router-dom'

const mapStateToProps = (state) => {
	return {
		isAuth: state.auth.isAuth

	}
}

const LoginForm = (props) => {
	const Input = Element('input')
	return (
		<form onSubmit={props.handleSubmit} className="form-login">
			<div className="form-login__col">
				<Field className='input' name='email' validate={[required]} component={Input} placeholder='Enter your Email' />
			</div>
			<div className="form-login__col">
				<Field className='input' name='password' type='password' validate={[required]} component={Input} placeholder='Enter password' />
			</div>
			<div className="form-login__col">
				<Field type={'checkbox'} name={'rememberMe'} validate={[required]} component={Input} />Remember me
			</div>
			{props.error && <div className="summary-error">
				{props.error}
			</div>
			}
			<button className='btn'>LOGIN</button>
		</form>
	)
}


const LoginReduxForm = reduxForm({

	form: 'login',
})(LoginForm)

const Login = (props) => {
	const onSubmit = (formData) => {

		props.login(formData.email, formData.password, formData.rememberMe)
	}
	if (props.isAuth === true) {
		return <Navigate to='/Profile' />
	}
	return (
		<div className="login">
			<h1 className="login__title">Login</h1>
			<LoginReduxForm onSubmit={onSubmit} />
		</div>
	)
}
