import '../Profile.css'

const CurrentUser = ({ avatar, name, aboutMe, lookingForAJobDescription }) => {
	return (
		<div className="currentUser">
			<img src={avatar} alt="Avatar" className="currentUser__avatar" />
			<div className="currentUser__body">
				<h2 className="currentUser__name">{name}</h2>
				<div className="speciality__info">
					<p>About Me: {aboutMe}</p>
					<p>JobDiscription: {lookingForAJobDescription}</p>
				</div>
			</div>
		</div>
	)
}

export default CurrentUser