import CurrentUser from "./CurrentUser";
import { connect } from 'react-redux';


function mapStateToProps(state) {
	return {
		avatar: state.profile.setUserProfile.photos.large,
		name: state.profile.setUserProfile.fullName,
		aboutMe: state.profile.setUserProfile.aboutMe,
		lookingForAJobDescription: state.profile.setUserProfile.lookingForAJobDescription,
	}
}

const CurrentUserContainer = connect(mapStateToProps)(CurrentUser);

export default CurrentUserContainer
